import React from 'react'
import { Grid } from '@material-ui/core'
import './project.css'

export default function Project(props) {
    const { showMedia } = props;
    console.log(props)
    return (
        <Grid item xs={12} sm={6} md={4} style={{padding: '2em'}}>
            <div style = {{width: '100%'}} style={{padding: '1em', borderRadius: '1em', boxShadow: '20px 20px 60px #d9d9d9, -20px -20px 60px #ffffff', display: 'flex', flexDirection: 'column'}}>
                <h2 style={{alignSelf: 'center'}}> {props.project.title} </h2>
                {props.project.image != "" && <span style={{cursor: 'pointer', color: '#2233FF'}} onClick={() => showMedia(props.project.image)}>Click For Media Content</span>}
                <p>{props.project.content}</p>
            </div>
        </Grid>
    )
}
