import React, { useState } from 'react'
import { Grid } from '@material-ui/core'
import Project from './Project'
import MediaDialog from './MediaDialog'
let projects = require('./projects.json')

export default function MainScreen() {
    const [showMediaDialog, setShowMediaDialog] = useState(false);
    const [showProjects, setShowProjects] = useState(false);
    const [mediaContent, setMediaContent] = useState({});

    const showMedia = (image) => {
        setMediaContent(image);
        setShowMediaDialog(true);
    }

    return (
        <div style={{height: "100vh", width: "100vw"}}>
            <Grid container style={{paddingLeft: '2em'}}>
                <Grid item xs={12} style={{display: 'flex', flexDirection: 'column'}}>
                    <h1>Applications On This Website</h1>
                    <ul s>
                        <li><a href='/precalculus/'>Study For The Mad Trig Minute Test For Precalculus AC</a></li>
                        <li><span style={{color: "#2233FF", cursor: 'pointer'}} onClick={()=>setShowProjects(!showProjects)}>Check Out Some Of My Projects</span></li>
                    </ul>
                </Grid>
                {showProjects && projects.map(((project, i) => <Project key={i} project={project} showMedia={showMedia} />))}
            </Grid>
            <MediaDialog open={showMediaDialog} onClose={() => {setShowMediaDialog(false)}} content={mediaContent} />
        </div>
    )
}
